<template>
  <el-container class="dashboard-container">
    <el-aside :width="isCollapsed ? '45px' : '180px'" class="sidebar" :class="{ collapsed: isCollapsed }">
      <div class="logo" v-if="!isCollapsed" style="text-align: center;">oci-helper</div>

      <!-- Toggle button inside the sidebar -->
      <div class="toggle-button" @click="toggleSidebar">
        <el-icon v-if="isCollapsed">
          <Expand/>
        </el-icon>
        <el-icon v-else>
          <Fold/>
        </el-icon>
      </div>

      <el-scrollbar>
        <el-menu
            :default-active="activeMenu"
            @select="handleSelect"
            router
            class="sidebar-menu"
            background-color="transparent"
        >
          <el-menu-item index="home">
            <i class="el-icon-house"></i>
            <span v-if="!isCollapsed"><el-icon><HomeFilled/></el-icon>主页</span>
            <span v-if="isCollapsed"><el-icon><HomeFilled/></el-icon></span>
          </el-menu-item>
          <el-menu-item index="user">
            <i class="el-icon-document"></i>
            <span v-if="!isCollapsed"><el-icon><UserFilled/></el-icon>配置列表</span>
            <span v-if="isCollapsed"><el-icon><UserFilled/></el-icon></span>
          </el-menu-item>
          <el-menu-item index="createTask">
            <i class="el-icon-s-management"></i>
            <span v-if="!isCollapsed"><el-icon><Finished/></el-icon>任务列表</span>
            <span v-if="isCollapsed"><el-icon><Finished/></el-icon></span>
          </el-menu-item>
          <el-menu-item index="ociLog">
            <i class="el-icon-s-operation"></i>
            <span v-if="!isCollapsed"><el-icon><List/></el-icon>服务日志</span>
            <span v-if="isCollapsed"><el-icon><List/></el-icon></span>
          </el-menu-item>
          <el-menu-item index="sysCfg">
            <i class="el-icon-s-operation"></i>
            <span v-if="!isCollapsed"><el-icon><Tools/></el-icon>系统配置</span>
            <span v-if="isCollapsed"><el-icon><Tools/></el-icon></span>
          </el-menu-item>
        </el-menu>
      </el-scrollbar>
    </el-aside>

    <!-- Main Content Area -->
    <el-container>
      <el-header class="header">
        <span></span>
        <div class="right-buttons">
          <el-button @click="toggleTheme" class="logout-button">{{ themeText }}</el-button>
          <button @click="showLogoutConfirm" class="logout-button">退出登录</button>
        </div>
      </el-header>
      <el-main>
        <router-view></router-view> <!-- Display area for switching content -->
      </el-main>
      <el-footer style="text-align: center;">
        <el-link href="https://github.com/Yohann0617" target="_blank">© Yohann All Rights Reserved</el-link>
      </el-footer>
    </el-container>
  </el-container>
</template>

<script>
import {Fold, Expand} from '@element-plus/icons-vue';
export default {
  components: {
    Fold,
    Expand,
  },
  mounted() {
    this.isDarkMode = localStorage.getItem('theme') === 'dark';
  },
  computed: {
    themeText() {
      return this.isDarkMode ? '切换白天' : '切换黑夜';
    }
  },
  data() {
    return {
      activeMenu: 'home',
      isCollapsed: false, // Controls sidebar state
      isDarkMode: localStorage.getItem('theme') === 'dark' // 读取本地存储的主题状态
    };
  },
  methods: {
    toggleTheme() {
      const isDark = document.documentElement.classList.toggle('dark')
      localStorage.setItem('theme', isDark ? 'dark' : 'light')
      this.isDarkMode = isDark; // 更新状态
    },
    handleSelect(index) {
      this.activeMenu = index;
      this.$router.push(`/dashboard/${index}`);
    },
    toggleSidebar() {
      this.isCollapsed = !this.isCollapsed;
    },
    showLogoutConfirm() {
      const confirmed = window.confirm('确定要退出登录吗?');
      if (confirmed) {
        this.logout();
      } else {
        this.$message.info('取消退出');
      }
    },
    logout() {
      sessionStorage.removeItem('token');
      this.$router.push('/login');
    }
  }
};
</script>

<style scoped>
.dashboard-container {
  height: 100vh;
}

.sidebar {
  background-color: #304156;
  color: #fff;
  display: flex;
  flex-direction: column;
  position: relative;
  transition: width 0.3s;
  overflow: hidden;
}

.sidebar.collapsed {
  width: 65px;
}

.toggle-button {
  width: 100%;
  height: 30px;
  background-color: rgba(242, 242, 242, 0.58);
  color: #304156;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-top: 10px;
}

.toggle-button:hover {
  background-color: #f2f2f2;
}

.logo {
  font-size: 24px;
  font-weight: bold;
  padding: 20px 0;
}

.sidebar-menu ::v-deep .el-menu-item {
  color: #ffffff;
}

.sidebar-menu ::v-deep .el-menu-item:hover,
.sidebar-menu ::v-deep .el-menu-item.is-active {
  background-color: rgba(255, 255, 255, 0.1);
  font-weight: bold;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  background-color: #304156;
  color: #fff;
  align-items: center;
  height: 60px;
  border-bottom: 1px solid #e6e6e6;
}

.logout-button {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
}

.logout-button:hover {
  color: #ff4d4f;
}

.right-buttons {
  margin-left: auto; /* 让按钮组靠右 */
  display: flex;
  gap: 10px; /* 按钮间距 */
}
</style>
